* {
    box-sizing: border-box;
}

body{
    margin:0;
    padding:0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #eff3f7;
    height:100%;
}

html,
body,
#potato,
#root,
.container{
    height: 100%;
    display: flex;
    justify-content: center;
}

.loader{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;
}

.movies{
    display:flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap:wrap;
    padding: 50px;
    padding-top: 70px;
    width: 80%;

}

.movies .movie{
    width:45%;
    background-color: white;
    margin-bottom: 70px;
    display:flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding: 20px;
    position: relative;
    border-radius: 5px;
    color: #adaeb9;
    box-shadow: 0 30px 60px 12px rgba(50,50,93,.25), 0 18px 36px -18px rgba(0,0,0,.3), 0 -12px 36px -8px rgba(0,0,0,.025);
}

.movie img{
    position: relative;
    top:-50px;
    max-width: 180px;
    margin-right: 30px;
    box-shadow: 0 30px 60px 12px rgba(50,50,93,.25), 0 18px 36px -18px rgba(0,0,0,.3), 0 -12px 36px -8px rgba(0,0,0,.025);
}

.movie .movie__title,
.movie .movie_year{
    margin:0;
    font-weight: 300;

}

.movie .movie__title{
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
}

.movie .movie__genres{
    list-style: none;
    padding:0px;
    margin:5 0;
    display: flex;
}

.movie__genres li{
    margin-right: 10px;
    font-size: 14px;
}